.transition {
  overflow: hidden;
}

.layout {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: none;
  transition: background-color 0.15s ease;
}

.form-wrapper {
  width: 300px;
  padding: 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  background: #fdfcfa;
  text-align: center;
  box-shadow: -2px 1px 5px 1px gray;
  border-radius: 30px;
}

.form-wrapper h1 {
  color: black;
  text-transform: uppercase;
  font-weight: 500;
}
